import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';

import {PropertyService} from '../../providers/property-service-mock';
import {UserProvider} from '../../providers/user/user';

@IonicPage({
	name: 'page-property-list1',
	segment: 'property-list1'
})

@Component({
    selector: 'page-property-list1',
    templateUrl: 'property-list1.html'
})
export class propertyListPage {

    favorites: Array<any> = [];
    data:any;
    propertyDetails:any;
    data1:any;


    constructor(public navCtrl: NavController, public navparam:NavParams, public user: UserProvider, public service: PropertyService) {
        // this.getFavorites();
        // console.log(this.favorites);

       this.propertyDetails = this.navparam.get('propertyDetails');
    //    this.propertyDetails = this.propertyDetails.property;
       this.data1 = this.propertyDetails.property;
       console.log(this.data1);




        //  this.user.propertylist().subscribe(data =>{
        //     let datas = JSON.parse(JSON.stringify(data));
        //     let data2 = JSON.parse(datas._body);
        //     this.data = data2.data;
        //     console.log(this.data);
           
           
        // })
    }

    itemTapped(favorite) {
		this.navCtrl.push('page-property-detail', {
			'id': favorite.property.id
		});
    }

    deleteItem(favorite) {
        this.service.unfavorite(favorite)
            .then(() => {
                this.getFavorites();
            })
            .catch(error => alert(JSON.stringify(error)));
    }

    getFavorites() {
        this.service.getFavorites()
            .then(data => this.favorites = data);
    }

    goToDetails(item){
        this.navCtrl.push('page-property-detail-list',{'item':item, 'propertyDetails':this.data1, 'propertyDetails1':this.propertyDetails});
    }

}



